import React, { useState, useEffect } from 'react';
import { Trophy, ArrowBigUp, Crown, Clock, AlertCircle, ChevronUp, Award, Users, TrendingUp, BarChart } from 'lucide-react';
import { createClient } from '@supabase/supabase-js';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';

const supabaseUrl = 'https://phqinpysykzgepksvjuc.supabase.co'
const supabaseKey = 'eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJpc3MiOiJzdXBhYmFzZSIsInJlZiI6InBocWlucHlzeWt6Z2Vwa3N2anVjIiwicm9sZSI6ImFub24iLCJpYXQiOjE3MzE0NTI2MTMsImV4cCI6MjA0NzAyODYxM30.nWK1_TTEClFtDtFshg-vrTU9Le_-Y_0eE1kB6Y83j2A'
const supabase = createClient(supabaseUrl, supabaseKey)

const App = () => {
  // États
  const [scores, setScores] = useState({
    brice: 0,
    cecile: 0
  });
  const [scoreHistory, setScoreHistory] = useState([]);
  const [winner, setWinner] = useState(null);
  const [lastUpdate, setLastUpdate] = useState(null);
  const [isUpdating, setIsUpdating] = useState(false);
  const [isWithinWorkHours, setIsWithinWorkHours] = useState(false);
  const [errorMessage, setErrorMessage] = useState('');
  const [showNotification, setShowNotification] = useState(false);
  const [notificationMessage, setNotificationMessage] = useState('');
  const [isInitialLoad, setIsInitialLoad] = useState(true);

  // Fonction pour ajouter un point dans l'historique
  const addToHistory = (briceScore, cecileScore) => {
    const now = new Date();
    const time = now.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
    setScoreHistory(prev => {
      const newHistory = [...prev, {
        time,
        Brice: briceScore,
        Cécile: cecileScore
      }];
      return newHistory.slice(-8); // Garde les 8 derniers points
    });
  };

  // Fonction pour afficher une notification temporaire
  const showTemporaryNotification = (message) => {
    setNotificationMessage(message);
    setShowNotification(true);
    setTimeout(() => setShowNotification(false), 3000);
  };

// Effets et gestionnaires d'événements
useEffect(() => {
  fetchScores();
  
  const channel = supabase.channel('custom-all-channel')
    .on(
      'postgres_changes',
      {
        event: '*',
        schema: 'public',
        table: 'scores',
      },
      (payload) => {
        if (payload.new) {
          const newScores = {
            brice: payload.new.brice,
            cecile: payload.new.cecile
          };
          setScores(newScores);
          if (!isInitialLoad) {
            addToHistory(newScores.brice, newScores.cecile);
          }
          setLastUpdate(new Date().toLocaleTimeString());
        }
      }
    )
    .subscribe();

  setIsInitialLoad(false);
  return () => {
    supabase.removeChannel(channel);
  };
}, [isInitialLoad]);

const checkWorkHours = () => {
  const now = new Date();
  const day = now.getDay();
  const hour = now.getHours();
  const minutes = now.getMinutes();
  const currentTime = hour * 60 + minutes;
  
  const startTime = 8 * 60 + 45;  // 8h45
  const endTime = 16 * 60 + 45;   // 16h45

  if (day === 0 || day === 6) {
    setErrorMessage('Le défi est actif uniquement en semaine !');
    return false;
  }

  if (currentTime < startTime || currentTime > endTime) {
    setErrorMessage('Le défi est actif de 8h45 à 16h45 !');
    return false;
  }

  setErrorMessage('');
  return true;
};

useEffect(() => {
  const checkTime = () => {
    setIsWithinWorkHours(checkWorkHours());
  };
  
  checkTime();
  const interval = setInterval(checkTime, 60000);
  return () => clearInterval(interval);
}, []);

useEffect(() => {
  if (scores.brice > scores.cecile) {
    setWinner('brice');
    showTemporaryNotification('🎉 Brice prend la tête !');
  } else if (scores.cecile > scores.brice) {
    setWinner('cecile');
    showTemporaryNotification('🎉 Cécile prend la tête !');
  } else {
    setWinner(null);
  }
}, [scores]);

const fetchScores = async () => {
  try {
    const { data, error } = await supabase
      .from('scores')
      .select('*')
      .limit(1)
      .single();
    
    if (error) throw error;
    
    if (data) {
      const newScores = {
        brice: data.brice,
        cecile: data.cecile
      };
      setScores(newScores);
      if (!isInitialLoad) {
        addToHistory(newScores.brice, newScores.cecile);
      }
      setLastUpdate(new Date().toLocaleTimeString());
    }
  } catch (error) {
    console.error('Erreur:', error);
  }
};

const incrementScore = async (player) => {
  if (!isWithinWorkHours) {
    alert(errorMessage || 'Vous ne pouvez pas incrémenter le score en dehors des heures de travail !');
    return;
  }

  if (isUpdating) return;
  setIsUpdating(true);

  try {
    const newScores = {
      ...scores,
      [player]: scores[player] + 1
    };
    setScores(newScores);
    addToHistory(newScores.brice, newScores.cecile);
    setLastUpdate(new Date().toLocaleTimeString());

    const { error } = await supabase
      .from('scores')
      .update({ 
        [player]: newScores[player],
        updated_at: new Date().toISOString()
      })
      .eq('id', 1);

    if (error) throw error;

  } catch (error) {
    console.error('Erreur:', error);
    fetchScores();
  } finally {
    setIsUpdating(false);
  }
};

const resetScores = async () => {
  if (!isWithinWorkHours) {
    alert('Vous ne pouvez pas réinitialiser les scores en dehors des heures de travail !');
    return;
  }

  const confirmation = window.confirm("Êtes-vous sûr de vouloir réinitialiser les scores ?");
  if (confirmation) {
    try {
      setScores({ brice: 0, cecile: 0 });
      setScoreHistory([]);
      setLastUpdate(new Date().toLocaleTimeString());

      const { error } = await supabase
        .from('scores')
        .update({ 
          brice: 0,
          cecile: 0,
          updated_at: new Date().toISOString()
        })
        .eq('id', 1);

      if (error) throw error;
      showTemporaryNotification('Scores réinitialisés !');

    } catch (error) {
      console.error('Erreur:', error);
      fetchScores();
    }
  }
};

return (
  <div className="min-h-screen bg-gradient-to-br from-indigo-100 via-purple-100 to-pink-100 p-4 md:p-8">
    {showNotification && (
      <div className="fixed top-4 right-4 bg-gradient-to-r from-green-500 to-emerald-500 text-white px-6 py-3 rounded-xl shadow-lg transform transition-all duration-300 z-50 animate-bounce">
        {notificationMessage}
      </div>
    )}

<div className="max-w-4xl mx-auto">
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold mb-4 text-transparent bg-clip-text bg-gradient-to-r from-purple-600 to-pink-600">
            Défi flemmard
          </h1>
          
          <div className={`inline-flex items-center gap-2 px-4 py-2 rounded-full ${
            isWithinWorkHours 
              ? 'bg-gradient-to-r from-green-500 to-emerald-500 text-white' 
              : 'bg-gradient-to-r from-red-500 to-pink-500 text-white'
          } shadow-lg transition-all duration-300`}>
            <Clock className="animate-pulse" size={20} />
            {isWithinWorkHours ? 'Défi actif' : 'Défi en pause'}
          </div>
        </div>

        {errorMessage && (
          <div className="mb-6 flex items-center justify-center gap-2 bg-red-100 border border-red-200 text-red-600 px-4 py-3 rounded-xl shadow-sm">
            <AlertCircle size={20} />
            {errorMessage}
          </div>
        )}
        
        <div className="grid grid-cols-1 md:grid-cols-2 gap-8 mb-8">
          {/* Cécile */}
          <div className="bg-white rounded-2xl p-6 shadow-xl transform transition-all duration-300 hover:shadow-2xl relative overflow-hidden">
            <div className="absolute top-0 left-0 w-full h-1 bg-gradient-to-r from-pink-300 to-pink-500"></div>
            {winner === 'cecile' && (
              <div className="absolute top-2 right-2 animate-bounce">
                <Crown className="text-yellow-500" size={28} />
              </div>
            )}
            <h2 className="text-2xl font-bold mb-4 text-pink-600">Cécile</h2>
            <div className="text-6xl font-bold mb-4 text-pink-800 transition-all duration-300">
              {scores.cecile}
            </div>
            <p className="text-gray-600 mb-4">levés du bureau</p>
            <button
              onClick={() => incrementScore('cecile')}
              disabled={!isWithinWorkHours || isUpdating}
              className={`group bg-gradient-to-r from-pink-400 to-pink-600 text-white px-6 py-3 rounded-full font-bold flex items-center justify-center gap-2 w-full transition-all duration-300 shadow-lg hover:shadow-xl ${
                !isWithinWorkHours && 'opacity-50 cursor-not-allowed'
              }`}
            >
              <ArrowBigUp className="transition-transform duration-300 group-hover:-translate-y-1" />
              Je me lève !
            </button>
          </div>

          {/* Brice */}
          <div className="bg-white rounded-2xl p-6 shadow-xl transform transition-all duration-300 hover:shadow-2xl relative overflow-hidden">
            <div className="absolute top-0 left-0 w-full h-1 bg-gradient-to-r from-blue-300 to-blue-500"></div>
            {winner === 'brice' && (
              <div className="absolute top-2 right-2 animate-bounce">
                <Crown className="text-yellow-500" size={28} />
              </div>
            )}
            <h2 className="text-2xl font-bold mb-4 text-blue-600">Brice</h2>
            <div className="text-6xl font-bold mb-4 text-blue-800 transition-all duration-300">
              {scores.brice}
            </div>
            <p className="text-gray-600 mb-4">levés du bureau</p>
            <button
              onClick={() => incrementScore('brice')}
              disabled={!isWithinWorkHours || isUpdating}
              className={`group bg-gradient-to-r from-blue-400 to-blue-600 text-white px-6 py-3 rounded-full font-bold flex items-center justify-center gap-2 w-full transition-all duration-300 shadow-lg hover:shadow-xl ${
                !isWithinWorkHours && 'opacity-50 cursor-not-allowed'
              }`}
            >
              <ArrowBigUp className="transition-transform duration-300 group-hover:-translate-y-1" />
              Je me lève !
            </button>
          </div>
        </div>

        {/* Graphique */}
        <div className="mb-8 bg-white rounded-2xl p-6 shadow-xl">
          <h3 className="text-xl font-bold mb-4 flex items-center gap-2 text-purple-800">
            <BarChart className="text-purple-500" />
            Évolution des scores
          </h3>
          <div className="h-64 w-full">
            <ResponsiveContainer width="100%" height="100%">
              <LineChart data={scoreHistory}>
                <CartesianGrid strokeDasharray="3 3" stroke="#f0f0f0" />
                <XAxis dataKey="time" stroke="#6b7280" />
                <YAxis stroke="#6b7280" />
                <Tooltip />
                <Legend />
                <Line 
                  type="monotone" 
                  dataKey="Brice" 
                  stroke="#3B82F6" 
                  strokeWidth={2}
                  dot={{ stroke: '#3B82F6', strokeWidth: 2 }}
                />
                <Line 
                  type="monotone" 
                  dataKey="Cécile" 
                  stroke="#EC4899" 
                  strokeWidth={2}
                  dot={{ stroke: '#EC4899', strokeWidth: 2 }}
                />
              </LineChart>
            </ResponsiveContainer>
          </div>
        </div>

        {/* Stats */}
        <div className="bg-white rounded-2xl p-6 shadow-xl">
          <h3 className="text-xl font-bold mb-6 flex items-center gap-2 text-purple-800">
            <Trophy className="text-purple-500" />
            Statistiques du jour
          </h3>
          <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-6">
            <div className="bg-gradient-to-br from-violet-50 to-purple-50 p-4 rounded-xl text-center">
              <Users className="mx-auto text-purple-500 mb-2" size={24} />
              <p className="text-gray-600 text-sm">Total des levés</p>
              <p className="text-2xl font-bold text-purple-800">{scores.brice + scores.cecile}</p>
            </div>
            <div className="bg-gradient-to-br from-violet-50 to-purple-50 p-4 rounded-xl text-center">
              <Award className="mx-auto text-purple-500 mb-2" size={24} />
              <p className="text-gray-600 text-sm">Différence</p>
              <p className="text-2xl font-bold text-purple-800">{Math.abs(scores.brice - scores.cecile)}</p>
            </div>
            <div className="bg-gradient-to-br from-violet-50 to-purple-50 p-4 rounded-xl text-center">
              <Clock className="mx-auto text-purple-500 mb-2" size={24} />
              <p className="text-gray-600 text-sm">Dernière mise à jour</p>
              <p className="text-sm font-medium text-purple-800">{lastUpdate}</p>
            </div>
          </div>
          <div className="text-center">
            <button
              onClick={resetScores}
              className="inline-flex items-center gap-2 text-red-500 hover:text-red-600 text-sm font-medium transition-colors duration-300"
              disabled={!isWithinWorkHours}
            >
              Réinitialiser les scores
            </button>
          </div>
        </div>
      </div>
    </div>
  );
};

export default App;


